import React, { useEffect, useState } from "react";
import styled from "styled-components";
// COMPONENTS
import Content from "./Content/Content";
import Breadcrumb from "../shared/Breadcrumb";
import { i18n } from "src/i18n";
import AproposService from "src/modules/Apropos/AproposService";
function Contact() {
  const langue = localStorage.getItem("language");
  const [apropos, setApropos] = useState("");
  useEffect(() => {
    AproposService.list("", "", "", "").then((res) => {
      if (langue === "fr") {
        setApropos(res.rows[0].contactsFR);
      }
      if (langue === "ar") {
        setApropos(res.rows[0].contactsAR);
      }
      if (langue === "en") {
        setApropos(res.rows[0].contactsEN);
      }
    });
  }, []);

  return (
    <>
      <ContactLayout>
        <Breadcrumb
          title={i18n("common.Contact")}
          items={[[i18n("app.title"), "/"], [i18n("common.Contact")]]}
        />
        <DescriptionSection>
          <p>{apropos}</p>
        </DescriptionSection>
        <Content />
      </ContactLayout>
    </>
  );
}

export default Contact;

const ContactLayout = styled.section`
  max-width: 1170px;
  margin: auto;
  @media screen and (max-width: 1024px) {
    margin: 160px 10px 0 10px;
  }
`;
const DescriptionSection = styled.div`
  color: var(--violet);
  font-size: 1rem;
  font-weight: 400;
  text-align: justify;
  margin: 28px 0 10px 0px;
`;
